import * as React from 'react';
import Layout from '../components/layout';

const AboutPage = () => {
  return (
    <Layout
      pageTitle="About Me"
      pageDescription="（Aboutページの説明）"
    >
      <p>こんにちは！ 私は GatsbyJSを試しているところです。</p>
    </Layout>
  )
};

export default AboutPage;